}

.homebtn {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.homebtn :after {
  content: '»';
  position: absolute;
  opacity: 0;  
  top: 14px;
  right: -20px;
  transition: 0.5s;
}

.homebtn :hover{
  padding-right: 24px;
  padding-left:8px;
