/* advisory css */
.pdf-container {
  margin-top: 110px;
  padding: 1rem;
  max-width: 800px;
  margin: 0 auto;
}

.pdf-header {
  margin-top: 110px;
  text-align: center;
  margin-bottom: 1rem;
}

.loading, .error-message {
  text-align: center;
  font-size: 1.2rem;
  margin-top: 2rem;
  color: #555;
}

.react-pdf__Page {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
  box-shadow: 0 0 8px rgba(0,0,0,0.2);
}


/* feedback form css */
.feedback-container {
  max-width: 600px;
  margin: auto;
  padding: 1rem;
  background: #f9f9f9;
  border-radius: 10px;
  margin-top: 110px; /* or padding-top: 110px */
}

.feedback-form {
  display: flex;
  flex-direction: column;
}

.feedback-form label {
  margin-bottom: 5px;
  font-weight: 600;
  color: #333;
}

.feedback-form input,
.feedback-form select,
.feedback-form textarea {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 1rem;
  width: 100%;
}

.feedback-form textarea {
  resize: vertical;
}

.feedback-form button {
  background-color: #2E97A7;
  color: white;
  border: none;
  padding: 12px;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
}

.feedback-form button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

@media (max-width: 600px) {
  .feedback-container {
    padding: 0.8rem;
  }

  .feedback-form button {
    font-size: 0.95rem;
    padding: 10px;
  }
}

